/* 전체적인 스타일 설정 */
body {
  margin: 0;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #141414;
  color: white;
}

/* 네비게이션 바 스타일 */
.navbar {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 25px;
  background-color: rgb(221, 77, 77);
  position: fixed; /* 네비게이션 바를 상단에 고정 */
  top: 0; /* 상단에 위치 */
  width: 100%; /* 전체 너비 */
  z-index: 1000; /* 다른 요소 위에 표시 */
}

/* 로고 스타일 */
.navbar .logo {
  font-size: 24px;
  font-weight: bold;
}

/* 메뉴 스타일 */
.navbar .menu {
  display: flex;
  gap: 20px;
  margin-left:20px;
}

/* 각 메뉴 아이템 스타일 */
.navbar .menu a {
  color: white;
  text-decoration: none;
  transition: color 0.3s;
}

.navbar .menu a:hover {
  color: #e50914; /* 호버 시 색상 변경 */
}

/* 메인 콘텐츠 스타일 */
.main-content {
  padding-top: 80px; /* 네비게이션 바를 피하기 위해 패딩 추가 */
}


/* 영화 리스트 스타일 */
.movie-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); /* 4열로 나열 */
  gap: 20px;
  padding: 20px;
}


/* 개별 영화 카드 스타일 */
.movie-card {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  transition: transform 0.3s;
}

.movie-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.movie-card:hover {
  transform: scale(1.05); /* 카드 호버 시 확대 효과 */
}

/* 영화 제목 스타일 */
.movie-title {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
  font-size: 16px;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
}

/* 푸터 스타일 */
.footer {
  background-color: #000;
  color: #888;
  text-align: center;
  padding: 20px;
  position: relative;
  bottom: 0;
  width: 100%;
}

/* 검색 입력 필드 스타일 */
.search-input {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  margin: 20px auto;
  display: block;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s;
}

.search-input:focus {
  border-color: lightcoral;
}
.genre-buttons {
  display: flex;
  gap: 10px;
  padding: 20px;
}

.genre-buttons button {
  padding: 10px 15px;
  font-size: 16px;
  color: white;
  background-color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.genre-buttons button:hover {
  background-color: rgb(221, 77, 77);
}

.sort-buttons {
  padding: 10px;
}

.sort-buttons button {
  padding: 10px 15px;
  font-size: 16px;
  color: white;
  background-color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.sort-buttons button:hover {
  background-color: #e50914;
}



