.movie {
    position: relative;
    width: 200px;
    text-align: center;
  }
  
  .movie-poster {
    position: relative;
    overflow: hidden;
  }
  
  .movie-poster img {
    width: 100%;
    height: auto;
    display: block;
    border-radius: 10px;
    transition: transform 0.3s;
  }
  
  .movie-poster:hover img {
    transform: scale(1.1);
  }
  
  .rating-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: white;
    background: rgba(0, 0, 0, 0.7);
    opacity: 0;
    transition: opacity 0.3s;
    border-radius: 10px;
  }
  
  .movie-poster:hover .rating-overlay {
    opacity: 1;
  }
  